import React, { FC, ReactElement, useEffect, useState } from "react";
import {
  FC_GetSchoolsPositions,
  FC_UpdateCandidatePosition,
  SchoolPositions,
} from "../../actions";
import Alert from "../Alert/Alert";
import Button from "../Buttons/Button";
import InputSelect from "../Inputs/InputSelect";
import Loading from "../Loading/Loading";
import Modal, { Themes, ModalSize } from "../Modal/Modal";

type UpdatePositionProps = {
  close: () => void;
  position_code: string | undefined | null;
  success: (role_id: string) => void;
  user_id: string;
  school_code: string | null | undefined;
};

const UpdatePosition: FC<UpdatePositionProps> = ({
  close,
  position_code,
  success,
  user_id,
  school_code,
}): ReactElement => {
  console.log({ position_code, user_id });

  const [position_code_input, setPositionInput] = useState("");
  const [qualification_id, setQualification] = useState("");
  const [loading, setLoading] = useState(true);
  const [loading_update, setLoadingUpdate] = useState(false);
  const [error, setError] = useState("");
  const [success_msg, setSuccessMsg] = useState("");
  const [position_list, setPositionList] = useState<SchoolPositions[]>([]);

  useEffect(() => {
    if (position_code_input === "" && position_code && loading === true)
      setPositionInput(position_code);
    console.log({ position_code });

    if (loading === true && school_code !== null && school_code !== undefined)
      FC_GetSchoolsPositions(school_code, (status, data, msg) => {
        setLoading(false);
        if (!status) setError(msg);
        setPositionList(data ? data : []);
      });
  }, [loading, position_code_input, position_code, school_code]);

  const UpdatePosition = (e: any) => {
    e.preventDefault();
    if (position_code_input === "") return setError("Please select a role");

    if (school_code === null || school_code === undefined)
      return setError("School code does not found.");

    setLoadingUpdate(true);
    FC_UpdateCandidatePosition(
      {
        position_code: position_code_input,
        user_id: user_id,
        qualification_id: qualification_id,
        school_code: school_code,
      },
      (status, data, msg) => {
        setLoadingUpdate(false);
        if (status === true) {
          setSuccessMsg("User Role has been updated");
          success(position_code_input);
        }
        if (status === false) setError(msg);
      }
    );
  };

  if (school_code === null || school_code === undefined)
    return (
      <Modal
        backDrop={true}
        theme={Themes.default}
        close={close}
        backDropClose={false}
        widthSizeClass={ModalSize.medium}
        displayClose={true}
        title="Change position"
        padding={{
          title: true,
          body: undefined,
          footer: undefined,
        }}
      >
        <div className="py-8 px-2 text-center">
          <h1 className="text-2xl font-bold">School code does not found</h1>
          <p className="mt-2 text-gray-700">
            The school_code doe not found in the system
          </p>
        </div>
      </Modal>
    );

  /* function body */
  return (
    <Modal
      backDrop={true}
      theme={Themes.default}
      close={close}
      backDropClose={false}
      widthSizeClass={ModalSize.medium}
      displayClose={true}
      title="Change position"
      padding={{
        title: true,
        body: undefined,
        footer: undefined,
      }}
    >
      <div className="border-t p-2">
        {error !== "" && (
          <Alert theme="danger" onClick={() => setError("")}>
            {error}
          </Alert>
        )}
        {success_msg !== "" && (
          <Alert theme="success" onClick={() => setSuccessMsg("")}>
            {success_msg}
          </Alert>
        )}

        {loading === true ? (
          <div className="text-center p-5">
            <Loading />
          </div>
        ) : (
          <div className="p-1">
            <form onSubmit={UpdatePosition}>
              <InputSelect
                onChange={(event: React.ChangeEvent<HTMLSelectElement>) => {
                  let foundTeacher = position_list.find(
                    (el) => el.position_code === event.target.value
                  );
                  if (foundTeacher) {
                    setPositionInput(foundTeacher.position_code);
                    setQualification(foundTeacher.qualification_id);
                  } else {
                    setError("Failed to select the position");
                  }
                }}
                value={position_code_input ? position_code_input : ""}
                error={""}
                label={"Select the position"}
                placeholder={""}
                name={""}
                data={[
                  {
                    title: "Select a position",
                    value: "",
                  },
                  ...position_list.map((el) => ({
                    title: `${el.position_code} - ${el.position_name} - ${el?.qualification_name}`,
                    value: el.position_code,
                  })),
                ]}
                disabled={loading_update}
              />
              <div className="pb-1.5 text-right">
                <Button type="submit" loading={loading_update}>
                  Update position
                </Button>
              </div>
            </form>
          </div>
        )}
      </div>
    </Modal>
  );
};
export default UpdatePosition;
